<template>
  <v-dialog
    v-model="isModalOpen"
    persistent
    max-width="600px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        color="primary float-right mt-2"
        dark
        v-bind="attrs"
        v-on="on"
      >
        <slot />
      </v-btn>
    </template>
    <v-card>
      <v-form
        @submit.prevent="onSubmit"
        ref="form"
        class="custom-form"
      >
        <v-card-title>
          <span class="headline grey--text">{{ title }}</span>
        </v-card-title>
        <v-container class="pa-5">
          <v-row>
            <v-col cols="12">
              <v-row>
                <v-col cols="4" class="py-0">
                  <label class="custom-form-label" :for="'room-type-title'">{{ $t('facility.roomTypeTitle') }}</label>
                </v-col>
                <v-col cols="8" class="py-0">
                  <v-text-field
                    id="room-type-title"
                    v-model="typeTitle"
                    :rules="rules.titleRequired"
                    dense
                    outlined
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4" class="py-0">
                  <label class="custom-form-label" :for="'room-number'">{{ $t('facility.numberOfRooms') }}</label>
                </v-col>
                <v-col cols="8" class="py-0">
                  <v-text-field
                    id="room-number"
                    type="number"
                    v-model="quantity"
                    min="1"
                    :rules="rules.minRooms"
                    dense
                    outlined
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="isModalOpen = false"
          >
            {{ $t('facility.cancel') }}
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            type="submit"
          >
            {{ $t('facility.saveAndCreate') }}
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'CreateRoomTypeModal',
  data () {
    return {
      title: this.$t('facility.creatingRoomType'),
      isModalOpen: false,
      valid: true,
      quantity: 1,
      typeTitle: '',
      rules: {
        titleRequired: [value => !!value || this.$t('rules.nameIsRequired')],
        minRooms: [value => value > 0 || this.$t('rules.minimumNCharacter', { value: 1 })]
      }
    }
  },
  methods: {
    onSubmit () {
      if (!this.$refs.form.validate()) {
        return
      }
      this.isModalOpen = false

      // it will be changed when backend is ready
      const routeData = this.$router.resolve({ path: '/facility/settings/43224/room-types/43323/edit' })
      window.open(routeData.href, '_blank')
    }
  }
}
</script>

