<template>
  <base-facility>
    <template v-slot:title>{{  $t('facility.data.facilitySettingsTitle') }}</template>
    <template v-slot:actions>
      <create-room-type-modal>{{ $t('buttons.createNewRoomType') }}</create-room-type-modal>
    </template>
    <template v-slot:default>
      <v-card class="v-card-responsive-table pa-5" shaped tile >
        <v-card-title class="grey--text mt-n5" v-once>
          <v-breadcrumbs
            large
            :items="breadcrumbItems"
            class="ma-0 pa-0 custom-breadcrumb"
            divider=">"
          ></v-breadcrumbs>
        </v-card-title>
        <v-simple-table class="custom-table d-flex mt-n4">
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">
                {{  $t('facility.roomTypeId') }}
              </th>
              <th class="text-left">
                {{  $t('facility.roomTypeName') }}
              </th>
              <th></th>
              <th class="text-left">
                {{  $t('facility.roomTypeSettings') }}
              </th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="item in roomTypes"
              :key="item.id"
            >
              <td>{{ item.id }}</td>
              <td class="font-weight-medium">{{ item.name }}</td>
              <td>
                <v-btn
                  color="secondary"
                  small
                  link
                  :to="'room-types/' + item.id + '/rooms'"
                >{{  $t('buttons.eachRoomSettings') }}</v-btn>
              </td>
              <td class="text-center">
                <v-btn
                  color="white"
                  elevation="2"
                  x-small
                  fab
                  :to="'/facility/settings/43224/room-types/' + item.id + '/edit'"
                  target="_blank"
                >
                  <v-icon color="secondary">
                    mdi-pencil
                  </v-icon>
                </v-btn>
              </td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>
    </template>
  </base-facility>
</template>

<script>
import BaseFacility from '@/components/Facilities/BaseFacility'
import CreateRoomTypeModal from '@/components/Facilities/RoomTypes/CreateModal'

export default {
  name: 'FacilityRoomTypesSettings',
  data () {
    return {
      roomTypes: [],
      breadcrumbItems: [
        {
          text: this.$t('facility.data.breadcrumbs.facilityList'),
          disabled: false,
          exact: true,
          to: '/facility/settings'
        },
        {
          text: this.$t('facility.data.breadcrumbs.kyoto'),
          disabled: true
        }
      ]
    }
  },
  mounted () {
    this.initRoomTypes()
  },
  methods: {
    initRoomTypes () {
      const roomType = this.$t('facility.data.noSmoking')
      for (let i = 0; i < 5; i++) {
        this.roomTypes.push({
          id: Math.floor(Math.random() * 90000) + 10000,
          name: roomType
        })
      }
    }
  },
  components: {
    CreateRoomTypeModal,
    BaseFacility
  }
}
</script>

<style scoped lang="scss">

</style>
